@import '../../style/variables.scss';

.char {
  &__info {
    padding: 25px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    position: relative;
    z-index: 5;
    transition: 0.3s transform;
    background-color: #fff;
    &-enter {
        opacity: 0;
    }
    &-enter-active {
        opacity: 1;
        transition: opacity 500ms ease-in;
    }
    &-exit {
        opacity: 1;
    }
    &-exit-active {
        opacity: 0;
        transition: opacity 500ms ease-in;
    }

    &__select {
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }

    &-name {
      font-weight: bold;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
    }

    .skeleton {
      margin-top: 30px;
    }
  }

  &__basics {
    display: grid;
    grid-template-columns: 150px auto;
    column-gap: 25px;
    transition: 0.3s transform;
    
    img {
      width: 150px;
      height: 150px;
    }
    &-enter {
        opacity: 0;
    }
    &-enter-active {
        opacity: 1;
        transition: opacity 500ms ease-in;
    }
    &-exit {
        opacity: 1;
    }
    &-exit-active {
        opacity: 0;
        transition: opacity 500ms ease-in;
    }
  }

  &__btns {
    margin-top: 35px;

    a:nth-child(2) {
      margin-top: 10px;
    }
  }

  &__descr {
    margin-top: 15px;
    font-size: 15px;
    line-height: 18px;
  }

  &__comics {
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    margin-top: 10px;

    &-list {
      position: relative;
      margin-top: 10px;
    }

    &-item {
      width: 100%;
      padding: 0px 10px;
      line-height: 25px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 10px;
    }
  }
}
.char__info-enter {
    opacity: 0;
  }
  
  .char__info-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  
  .char__info-exit {
    opacity: 1;
  }
  
  .char__info-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
  }
